import { WarnOnceHandler } from "./warnOnce";

export interface NavIdProps {
  /**     ( id) */
  nav?: string;
  id?: string;
}

export function getNavId(props: NavIdProps, warn?: WarnOnceHandler) {
  const id = props.nav || props.id;
  if (process.env.NODE_ENV === "development" && !id && warn) {
    warn('     "nav"  "id"', "error");
  }
  return id;
}
